import React, { useEffect, Suspense } from 'react';
import * as THREE from 'three';
import { Canvas, useFrame } from '@react-three/fiber';
import { useGLTF, useAnimations } from '@react-three/drei';

const model = 'https://jsd.cdn.zzko.cn/gh/coolfishstudio/files@mytab/app-r3f/models/duck/model.glb';

useGLTF.preload(model);

const Duck = ({ ...props }) => {
  const { scene, animations } = useGLTF(model);
  const { actions }: any = useAnimations(animations, scene);
  useEffect(() => void (actions['LironShuba'].reset().fadeIn(0.5).play()), [actions]);
  useFrame((state, delta) => {
    const action = actions['LironShuba'];
    state.camera.position.set(Math.cos((state.clock.elapsedTime * Math.PI) / 5) * 25, 1, Math.sin(state.clock.elapsedTime) * 15 + 35);
    state.camera.lookAt(0, 0, 0);
  });
  return (
    <primitive object={scene} {...props} />
  );
};

export default function App() {
  return (
    <Canvas camera={{ position: [-10, 10, 50], fov: 50 }}>
      <color attach='background' args={['#383948']} />
      <Suspense fallback={null}>
        <Duck scale={10} position={[0, -10, 0]} />
      </Suspense>
    </Canvas>
  );
};
